import React from "react";
import Layout from "../../common/Layout";

function Services() {
  return (
    <Layout>
      <div class="media my-3 mb-5 tm-service-media tm-service-media-img-l">
        <img src="img/services-1.jpg" alt="Image" class="tm-service-img" />
        <div class="media-body tm-service-text">
          <h2 class="mb-4 tm-content-title">Best Services for you</h2>
          <p>
            Our template is simple and effective. This is an alternating simple
            content with a squared size image. Menu hover is #9CC and text #FFF
          </p>
        </div>
      </div>
      <div class="media my-3 mb-5 tm-service-media">
        <div class="media-body tm-service-text">
          <h2 class="mb-4 tm-content-title">Customer Satisfaction</h2>
          <p>
            Feel free to contact TemplateMo for any kind of question. If you
            wish to support us, you can make a PayPal donation. Thank you.
          </p>
        </div>
        <img src="img/services-2.jpg" alt="Image" class="tm-service-img-r" />
      </div>
      <div class="media my-3 tm-service-media tm-service-media-img-l">
        <img src="img/services-3.jpg" alt="Image" class="tm-service-img" />
        <div class="media-body tm-service-text">
          <h2 class="mb-4 tm-content-title">Template Usage</h2>
          <p>
            You are free to use this template for your website either commercial
            or personal.
          </p>
          <p>
            You are NOT allowed to redistribute this template ZIP file for a
            download purpose on any template collection website.
          </p>
        </div>
      </div>
    </Layout>
  );
}

export default Services;
